import React from "react";
import { useAppSelector } from "redux/hooks/useTypedSelector";
import formate from "../utils/formate";
import {
  Container,
  List,
  Item,
  Day,
  Icon,
  Temperature,
  Wrapper,
} from "../styles/StyledDaily";
import { getIcon } from "utils/functions";
import DailyInfo from "./DailyInfo";

const Daily: React.FC = () => {
  
  const data = useAppSelector(state => state.oneCall.daily)
 
  return (
   <Container>
      <List>
        {data.map(({ temp: { day }, weather, dt }, index) => { 
          return (
            <Wrapper key={index} data-tooltip >
              <Item>
                <Icon
                  src={getIcon(weather[0].icon)}
                  alt="Иконка погоды"
                />
                <Day>
                  {index === 0
                    ? "Сегодня" :
                    index === 1
                    ? "Завтра"
                    : formate.dayWeek(dt + 1)}
                </Day>
                <Temperature>{Math.round(day)}°</Temperature>
                <DailyInfo dataWeather={data[index]}/>

              </Item>
            </Wrapper>
          );
        })}
      </List>
    </Container>
  );
}

export default Daily
