import React from "react";
import { useAppSelector } from "redux/hooks/useTypedSelector";
import { Container, Description, Info, InfoTitle, InfoValue, Sunrise, /*Sunrise,*/ Sunset, /*Sunset,*/ Wrapper } from "styles/StyledDailyInfo";
// import { Description, Text, Time } from "styles/StyledSunrise&Sunset";
import formate from "utils/formate";
import { formatePressure, formateToUpperCase } from "utils/functions";

type CardWeatherInfoProps = {
    dataWeather: any
}

const DailyInfo: React.FC<CardWeatherInfoProps> = ({ dataWeather }) => {

    const { timezone_offset }  = useAppSelector(state => state.oneCall)

    const {sunrise, sunset, wind_speed, pressure, humidity, pop, rain, weather: [ { description } ], temp: { max, min }} = dataWeather
  
    return (
        <Container>
        <Description>{formateToUpperCase(description)}</Description>
        {pop !== 0
            ? (<Info icon={rain ? "Rain" : "Snow" }>
                    <InfoTitle>Вероятность осадков</InfoTitle>
                    <InfoValue>{formate.precipitation(pop)}</InfoValue>
                </Info>)
            : ("")
        }
        <Info icon="Temp">
            <InfoTitle>Температура</InfoTitle>
            <InfoValue>{Math.round(min)}° - {Math.round(max)}°</InfoValue>
        </Info>
        <Info icon="Wind">
            <InfoTitle>Ветер</InfoTitle>
            <InfoValue>{`${Math.round(wind_speed)} м/с`}</InfoValue>
        </Info>
        <Info icon="Barometer">
            <InfoTitle>Давление</InfoTitle>
            <InfoValue>{formatePressure(pressure)}</InfoValue>
        </Info>
        <Info icon="Humidity">
            <InfoTitle>Влажность</InfoTitle>
            <InfoValue>{humidity}%</InfoValue>
        </Info>
        
        <Wrapper>
          <Sunrise icon="Sunrise">Восход {formate.time(sunrise, timezone_offset)}</Sunrise>
          <Sunset icon="Sunset">Закат {formate.time(sunset, timezone_offset)}</Sunset>
        </Wrapper>
        
        </Container>
    );
}

export default DailyInfo