import styled from "styled-components";
import { Item } from "./StyledDaily";
import Precipitation from "../images/svg/cloud-drizzle.svg";
import Temp from "../images/svg/wi_thermometer-celsius.svg";
import Wind from "../images/svg/wi_windsock.svg";
import Barometer from "../images/svg/wi_barometer.svg";
import Humidity from "../images/svg/wi_humidity.svg";
import Rain from "../images/svg/wi_rain.svg";
import Snow from "../images/svg/wi_snow.svg";
import SunriseIcon from "../images/svg/wi_sunrise.svg";
import SunsetIcon from "../images/svg/wi_sunset.svg";


interface IPropsInfoTitle {
  icon: "Precipitation" | "Temp" | "Wind" | "Barometer" | "Rain" | "Snow" | "Sunrise" | "Sunset" | "Humidity"
}

const handleIconType = (icon: string) => {
  switch (icon) {
    case "Precipitation":
      return `url(${Precipitation})`;
    case "Temp":
      return `url(${Temp})`;
    case "Wind":
      return `url(${Wind})`;
    case "Barometer":
      return `url(${Barometer})`;
    case "Rain":
      return `url(${Rain})`;
    case "Snow":
      return `url(${Snow})`;
    case "Sunrise":
      return `url(${SunriseIcon})`;
    case "Sunset":
      return `url(${SunsetIcon})`;
    case "Humidity":
      return `url(${Humidity})`;
  }
  return undefined
};

const Container = styled.div`
  ${Item} & {
    position: absolute;
    width: 380%;
    left: 0; top: 0; 
    pointer-events: none;
    opacity: 0; 
    transition: 1s;

    min-height: 100%;
    padding: 15px;
    border-radius: var(--br-20);

    background-color: var(--background-color);
    box-shadow: var(--box-shadow);
    backdrop-filter: var(--backdrop-filter);
    -webkit-backdrop-filter: var(--webkit-bf);
    border: var(--border);
  }
    ${Item}:hover & {
      opacity: 1;
      left: 110%; 
      top: 15%;
      z-index: 4;
    }
`;
const Description = styled.p`
  margin: 0 0 30px 17px;
  display: flex;
  flex-direction: row;
  font-size: 22px;
`
const Info = styled.div<IPropsInfoTitle>`
  display: flex;
  justify-content: space-between;
  margin-bottom: 7px;
  font-size: 14px;
  &:last-child {
    margin: 0;
  }
  &::before {
    content: ${({ icon }) => handleIconType(icon)};
    width: 20px;
    height: 20px;
    margin: auto 0.3rem auto auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: contain;
  }
`
const InfoTitle = styled.p`
  width: 50%;
  margin: 0 15px 0 0;
  display: flex;
  flex-direction: row;
`
const InfoValue = styled.p`
  width: 50%;
  margin: 0 auto 0 0;
  padding: 0 0 0 15px;
  display: flex;
  flex-direction: row;
  justify-content: end;
  border-left: 1px solid black;
`
const Wrapper = styled.div`
    width: 100%;
    margin: 30px 0 0 0;
    display: flex;
    flex-direction: row;
    align-content: space-between;
    justify-content: space-between;
`;
const Sunrise = styled.div<IPropsInfoTitle>`
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 18px;
    &::before {
    content: ${({ icon }) => handleIconType(icon)};
    width: 30px;
    height: 30px;
    margin: 0 12px 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: contain;
  }
`;
const Sunset = styled(Sunrise)`
    &::before {
    margin: 0 12px 0 auto;
  }
`;

export {
  Container,
  Description,
  Info,
  InfoTitle,
  InfoValue,
  Wrapper,
  Sunrise,
  Sunset
};
